html, body {
  margin: 0;
  padding: 0;
  min-height: 100%;
}

h1 {
  padding: 20px 0;
  margin: 0;
  clear: both;
  text-align: center;
}

h1, label {
  font-family: 'Slabo 27px', serif;
  color: #111111;
}

label, input {
  font-size: 14px;
}

input {
  outline: none;
}

ul, li {
  list-style: none;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 120px;
  padding-bottom: 60px;
}

.container:after {
  content: " ";
  display: block;
  clear: both;
}

.header {
  position: fixed;
  top: 0;
  background-color: white;
}

.header, .panel {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 100%;
  float: left;
  padding-left: 10%;
  padding-right: 10%;
}

.control .control-item {
  margin-bottom: 10px;
  padding: 5px;
  text-align: justify;
}

.control .control-item input, .control .control-item label {
  vertical-align: middle;
}

.control .control-item label span:nth-child(1) {
  text-decoration: underline;
}

.control .control-item label span:nth-child(2) {
  display: inline-block;
  width: 100%;
  margin-top: 10px;
}

.control .control-item:hover {
  background-color: #ededed;
  cursor: pointer;
}

.control .control-item:hover input, .control .control-item:hover label {
  cursor: pointer;
}

.colors {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 100%;
  float: left;
  padding-left: 10%;
  padding-right: 10%;
}

.colors li {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 33.3333333333%;
  float: left;
  padding-left: 3.3333333333%;
  padding-right: 3.3333333333%;
  padding-left: 0px;
  padding-right: 0px;
}

@media (min-width: 1024px) {
  .container {
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 0;
  }
  .container:after {
    content: " ";
    display: block;
    clear: both;
  }
  .header {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 50%;
    float: left;
    padding-left: 0.3205128205%;
    padding-right: 0.3205128205%;
    margin-left: 25%;
    position: relative;
  }
  .panel {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 50%;
    float: left;
    padding-left: 0.3205128205%;
    padding-right: 0.3205128205%;
    padding-left: 25px;
    padding-right: 25px;
    margin-top: 50px;
  }
  .control ul {
    max-height: 300px;
    overflow: auto;
  }
  .colors {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    float: left;
    padding-left: 0.3205128205%;
    padding-right: 0.3205128205%;
  }
  .colors li {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 25%;
    float: left;
    padding-left: 0.9615384615%;
    padding-right: 0.9615384615%;
    padding-left: 0px;
    padding-right: 0px;
  }
  .colors li label {
    width: 100%;
    color: white;
    display: inline-block;
    padding: 10px;
    box-sizing: border-box;
    cursor: pointer;
  }
  .colors li input {
    display: none;
  }
  .colors li input:checked + label {
    display: none;
  }
}
